<template>
    <div>
        <div>aaa</div>
       <button @click="getv">aaa</button>
       <button @click="geter">dd</button>
       <component v-bind:is="bb"></component>
    </div>
</template>

<script>
import BBB from './bbb.vue'
import CCC from './ccc.vue'
export default {
    data () {
        return {
           bb:'CCC'
        }
    },
    components: {
      BBB,  
      CCC
    },
methods: {
    getup(){
    this.bool=true
    },
    geter(){
        this.bb='CCC'
    },
    getv(){
        this.bb='BBB'
    }
},
}
</script>

<style>
.aa {
    position: relative;
    z-index: 10;
}
.onepirce {
    width: 10px;
    height: 10px;
    background: red;
    border-radius: 50%;
    margin: 0 auto;
    position: absolute;
    top: 0;
    left: 49%;
}
.acc {
    bottom: 0;
}
</style>



